@import url(https://fonts.googleapis.com/css2?family=Aldrich&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Aldrich&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  background: url(/radio-browser/static/media/earth.7e666962.jpg) no-repeat center center fixed;
  background-size: cover;
  margin: 0;
  padding: 15px;
  overflow-y: auto;
}
::-webkit-scrollbar-track {
  background: black; 
}
h1{
  color: rgb(190, 190, 190);
  font-size: 50px;
  font-family: 'Aldrich', sans-serif;
}
.App {
  text-align: center;
}
.nav{
  height: 15vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.genres{
    display :-webkit-flex;
    display :flex;
    -webkit-flex-direction : column;
            flex-direction : column;
    height: 150vh;
    width: 100%;
}
.genre-wrap{
    background-color: rgb(190, 190, 190);
    border-radius: 25px;
    height: 10vh;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 20px 10px;
}
.genre{
  color: black;
  font-size: 28px;
  font-family: 'Aldrich', sans-serif;
}
select{
  padding: 5px 10px;
  width: 15%;
  border-radius: 10px;
  text-align-last: center;
  font-family: 'Aldrich', sans-serif;
  cursor: pointer;
}
.options{
    color: white;
    background-color: black;
    cursor: pointer;
}
.station-wrap{
    min-height:130vh;
    margin-bottom: 10px;
    width: 100%;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content : center;
            justify-content : center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.station{
  background-color: rgb(190, 190, 190);
  border-radius: 25px;
  padding: 10px;
  margin: 10px;
  width: 100px;
  min-height: 80px;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content : center;
          justify-content : center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 1 1 350px;
          flex: 1 1 350px;
}
.stationName{
  padding: 10px;
  font-size: 20px;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  font-family: 'Aldrich', sans-serif;
}
.player-wrap{
  padding-bottom: 20px;
  width: 80%;
}
@media only screen and (max-width:768px){
  .genre-wrap{
    padding:10px;
  }
  select{
    width: 25%;
  }
  .options{
    font-size: 5px;
    color: white;
    background-color: black;
  }
  .genre{
    font-size: 23px;
  }
}


